import React from 'react'
import './Home.css'
import Main from '../../components/layout/Main'
import Aux from '../../components/layout/AuxDiv'
// import fig1 from '../../assets/imgs/combinacoes.png'
import fig1 from '../../assets/imgs/parametrizations.png'
import fig2 from '../../assets/imgs/media.png'
import fig3 from '../../assets/imgs/prob.png'

export default props =>
    <React.Fragment>
        <Aux></Aux>
        <Aux></Aux>
        <Aux></Aux>
        <Aux></Aux>
        <Main icon='home' title='WRF-Vis'
            subtitle='Sistema de visualizao de dados'>
            <div className='p-3 mt-3' id='home'>
                <h2>Sistema para a investigao das tendncias de parametrizaes de processos fsicos na produo de chuva</h2>
                <hr />
                <h4 className='mb-3 mt-3'>1. Introduo</h4>
                <p className='mb-2 ml-4'>
                Na modelagem numrica do tempo, h processos fsicos que no so resolvidos explicitamente pelos modelos, 
                ento, esquemas de parametrizao so utilizados para represent-los. 
                Existe uma srie de possveis parametrizaes para cada processo fsico, entretanto, na configurao de uma simulao, 
                 preciso selecionar uma delas, ou seja, uma para cada processo. 
                Essa deciso depende do evento a ser estudado e do conhecimento da influncia dos esquemas de parametrizao nos resultados da simulao em determinado local. 
                Esse conhecimento pode ser obtido gerando um conjunto de simulaes para a rea e a data de interesse, cada uma utilizando uma parametrizao distinta para cada processo fsico e, ao final, a comparao dos resultados. 
                Como no existe esse tipo de estudo para a Regio Serrana do Estado do Rio de Janeiro (RSRJ), que  um lugar com histrico de desastres ambientais oriundos de eventos severos de chuva, esse experimento foi realizado para essa localidade. 
                Esse sistema de visualizao, por sua vez, foi desenvolvido para auxiliar os especialistas em modelagem numrica do tempo a investigarem os resultados desse experimento em busca da influncia das principais amostras de parametrizaes de processos fsicos na modelagem da chuva na RSRJ.
                </p>
                <h4 className='mb-3 mt-3'>2. Caractersticas do Conjunto de Simulaes</h4>
                <p className='mb-2 ml-4'>
                O experimento foi realizado com o modelo WRF, verso 4.2.1, no perodo de 11/01/2011 00:00Z  12/01/2011 06:00Z 
                (horizonte temporal de trinta horas). Primeiramente, foram selecionados os processos fsicos que sofreriam modificaes 
                na sua representao: microfsica de nuvens, cumulus, camada de superfcie, superfcie terrestre e camada limite planetria. 
                Para cada um, foi selecionada uma amostra de parametrizaes mais utilizadas no meio cientfico, apresentadas na Tabela 1. 
                Cada simulao foi configurada considerando uma parametrizao para cada processo fsico, como exemplificado na Tabela 2.

                </p>
                <p className='mb-2 mt-3 ml-4 text-center'>
                    <i>Tabela 1. Processos Fsicos e amostras de parametrizaes</i>
                </p>
                <table align='center' border="1">
                    <tbody>
                        <tr>
                            <td className='text-center'><b>Processos Fsicos</b></td>
                            <td className='text-center'><b>Amostras de Parametrizaes</b></td>
                        </tr>
                        <tr>
                            <td className='p-1'>Cumulus</td>
                            <td className='p-1'>Kain-Fritsch (1), Betts-Miller-Janijc (BMJ) (2), Grell-Freitas (3), Grell-3D (5), Grell-Devenyi (93)</td>
                        </tr>
                        <tr>
                            <td className='p-1'>Microfsica de Nuvens</td>
                            <td className='p-1'>Kessler (1), Eta (Ferrier) (5), WSM6 (6()), Goddard (7)</td>
                        </tr>
                        <tr>
                            <td className='p-1'>Superfcie Terrestre</td>
                            <td className='p-1'>Dudhia (1), Noah MP (4)</td>
                        </tr>
                        <tr>
                            <td className='p-1'>Camada de Superfcie</td>
                            <td className='p-1'>MM5 (1), MM5 Old (91)</td>
                        </tr>
                        <tr>
                            <td className='p-1'>Camada Limite Planetria</td>
                            <td className='p-1'>MYNN3 (6), MRF (99)</td>
                        </tr>
                    </tbody>

                    </table>
                <p className='mb-2 mt-4 ml-4 text-center'>
                    <i>Tabela 2. Exemplo de configurao de parametrizaes dos processos fsicos de duas simulaes hipotticas</i>
                </p>
                    <table align='center' border="1">
                    <tbody>
                        <tr>
                            <td className='p-1'></td>
                            <td className='p-1 text-center'><b>Cumulus</b></td>
                            <td className='p-1 text-center'><b>Microfsica de Nuvens</b></td>
                            <td className='p-1 text-center'><b>Superfcie Terrestre</b></td>
                            <td className='p-1 text-center'><b>Camada de Superfcie</b></td>
                            <td className='p-1 text-center'><b>Camada Limite Planetria</b></td>
                        </tr>
                        <tr>
                            <td className='p-1 text-center'><b>Simulao 1</b></td>
                            <td className='p-1 text-center'>Kain-Fritsch</td>
                            <td className='p-1 text-center'>Kessler</td>
                            <td className='p-1 text-center'>Dudhia</td>
                            <td className='p-1 text-center'>MM5</td>
                            <td className='p-1 text-center'>MYNN3</td>
                        </tr>
                        <tr>
                            <td className='p-1 text-center'><b>Simulao 2</b></td>
                            <td className='p-1 text-center'>BMJ</td>
                            <td className='p-1 text-center'>Eta (Ferrier)</td>
                            <td className='p-1 text-center'>Noah MP</td>
                            <td className='p-1 text-center'>MM5</td>
                            <td className='p-1 text-center'>MYNN3</td>
                        </tr>
                    </tbody>
                    </table>
                <p className='mb-2 mt-4 ml-4'>
                Foram consideradas todas as possibilidades de combinao dessas amostras, ento, o total de simulaes  exatamente 
                a quantidade de combinaes possveis, resultante da multiplicao do tamanho das amostras, ou seja, 5 x 4 x 2 x 2 x 2, 
                conforme explicitado na Figura 1. Sendo assim, o conjunto (ensemble)  formado por cento e sessenta simulaes (membros). 
                </p>
                <p className='mb-2 ml-4' align='center'>
                    <img src={fig1} alt="some text" width='50%' height='50%'/>
                </p>
                <p className='mb-2 ml-4' align='center'>
                <i>Figura 1: Clculo do total de possibilidades de combinaes de parametrizaes de processos fsicos.</i>
                </p>

                <h4 className='mb-3 mt-3'>3. Utilizando o Sistema</h4>
                <p className='mb-2 ml-4'>
                Esse sistema  composto por dois mdulos: Mdia e Probabilidade. Para acessar o primeiro mdulo, o usurio deve clicar com 
                o boto esquerdo do mouse no cone Mdia, localizado do lado esquerdo da barra de navegao, na parte superior da tela. 
                Para acessar o segundo mdulo, o usurio realiza o mesmo procedimento, mas considerando o cone Probabilidade. 
                A seguir, o funcionamento de cada mdulo  descrito detalhadamente.
                </p>
                <p className='mb-2 ml-4'>
                <b>Observao</b>: Em ambos os mdulos, as anlises ocorrero por amostra de parametrizaes, ou seja, o usurio escolher 
                um processo fsico - microfsica de nuvens, cumulus, camada de superfcie, superfcie terrestre e camada limite planetria 
                - e o sistema selecionar a amostra de parametrizaes relacionadas a ele.  A ttulo de exemplo, se for escolhido o processo 
                fsico Microfsica de Nuvens, o sistema selecionar a amostra das parametrizaes Kessler, Eta (Ferrier), WSM6 e Goddard. 
                Em seguida, o sistema organizar o ensemble em subconjuntos, um para cada parametrizao da amostra. Na hiptese de o usurio 
                selecionar Microfsica de Nuvens, o sistema criar quatro subconjuntos, um identificado como Kessler, outro como 
                Eta (Ferrier), outro como WSM6, e mais um como Goddard. Os membros pertencero ao subconjunto da parametrizao que utilizaram 
                em suas configuraes. Nesse exemplo, cada subconjunto ter quarenta membros, pois o ensemble possui cento e sessenta membros 
                e a amostra possui quatro parametrizaes, logo, 160 / 4 = 40. Dessa forma, as anlises sero realizadas a partir dos 
                subconjuntos criados pelo sistema.
                </p>

                <h5 className='mb-3 mt-3 ml-4'>3.1 Mdulo Mdia</h5>
                
                    <p className='mb-2 ml-4'>3.1.1. Na barra de navegao, do lado direito da tela, o usurio encontrar um boto do tipo dropdown para selecionar o processo fsico que deseja investigar. Por padro, o sistema comea com o processo fsico Cumulus selecionado; </p>
                    <p className='mb-2 ml-4'>3.1.2. Escolhido o processo fsico, o sistema selecionar a amostra de parametrizaes relacionadas a ele, criar os subconjuntos de membros e montar a interface.</p>
                    <p className='mb-2 ml-4'>3.1.3.	Nessa interface, a tela  dividida em trs quadros que interagem entre si: Quadro A (Matrizes de Mdias de Chuva no Espao por Membro), Quadro B (Quadro de Mapas) e Quadro C (Mdias de Chuva por Parametrizao), conforme Figura 2.</p>

                    <p className='mb-2 ml-4' align='center'>
                    <img src={fig2} alt="some text" width='50%' height='50%'/>
                    </p>
                    <p className='mb-2 ml-4' align='center'>
                    <i>Figura 2: Exemplo da tela do sistema criado para o mdulo Mdia na anlise do processo MIcrofsica de Nuvens. Quadro A (lado direito superior): Matrizes de Mdias de Chuva no Espao por Membro; Quadro B (lado esquerdo): Mapas; Quadro C: Mdias de Chuva por Parametrizao.</i>
                    </p>
                <p className='mb-2 ml-4'>3.1.4. <u>Quadro A</u>: apresenta uma matriz para cada subconjunto de membros, ou seja, se o processo fsico escolhido for Microfsica de Nuvens, aparecero quatro matrizes. Cada matriz  assim caracterizada:</p>
                <ul className='mb-2 ml-4'>
                    <li>cada coluna da matriz representa um membro, ou seja, na hiptese de o processo fsico escolhido ter sido Microfsica de Nuvens, cada matriz apresentar quarenta colunas;</li>
                    <li>cada linha da matriz representa um instante de tempo e esto organizadas em ordem crescente de baixo para cima, ou seja, a linha da base representa o tempo 0h e a linha do topo o tempo 30h;</li>
                    <li>a cor de cada clula representa o volume de chuva mdio que aquele membro simulou naquele instante de tempo. 
                        O usurio tem a opo de visualizar esses valores em escala local selecionando o boto Escala Local ou 
                        Escala Local (todos) no canto superior esquerdo do Quadro A (quadro de matrizes), sendo o segundo boto til para 
                        alterar a escala dos trs quadros simultaneamente. Observao: De incio, esses valores so referentes a toda a regio 
                        para a qual as simulaes foram realizadas, entretanto, o usurio poder explorar reas especficas pertencentes a ela 
                        selecionando-as em um dos mapas do Quadro B.</li>
                </ul>
                <p className='mb-2 mt-3 ml-4'>
                Aps a explorao das matrizes, o usurio dever escolher um instante de tempo e dois subconjuntos por vez para realizar 
                investigaes destes na dimenso espacial.  Para isso, foi includa no lado direito do Quadro A uma coluna de cones 
                identificados que representam os instantes de tempo e esto alinhados com as linhas das matrizes. Clicando em um dos cones, 
                o usurio seleciona um tempo. Alm disso, abaixo de cada matriz encontram-se dois botes: Mapa 1 e Mapa 2. 
                O usurio dever selecionar o Mapa 1 de um subconjunto e o Mapa 2 de outro subconjunto. Feito isso, o Quadro B  atualizado 
                e o usurio volta sua ateno para ele. O usurio poder repetir esse passo a qualquer momento para atualizar o Quadro B. 
                O instante de tempo selecionado nesse Quadro tambm atualiza os valores de uma tabela que se encontra no Quadro C.
                </p>
                <p className='mb-2 mt-3 ml-4'>
                3.1.5.	<u>Quadro B</u>: No Mapa 1 aparecem os valores mdios de chuva previstos pelos membros pertencentes ao subconjunto selecionado 
                com o boto Mapa 1 no Quadro A, no instante de tempo escolhido. Ou seja, para esse instante de tempo, foi realizada uma mdia 
                dos valores de chuva simulados pelos membros daquele subconjunto para cada ponto de grade. O mesmo ocorre para o Mapa 2, sendo 
                o subconjunto referente ao que foi selecionado pelo boto Mapa 2 do Quadro A. O Mapa 3 apresenta a diferena entre os valores 
                do Mapa 1 e do Mapa 2, nessa ordem. Em qualquer um dos trs mapas,  possvel selecionar uma regio especfica e os valores do 
                Quadro A e do Quadro C so atualizados considerando apenas a rea selecionada. Para verificar outros subconjuntos ou outro instante 
                de tempo, o usurio deve retornar ao Quadro A e atualizar suas escolhas. O usurio tem a opo de visualizar os valores dos Mapas 1 
                e 2 em escala local selecionando o boto Escala Local na parte superior esquerda do Quadro B.
                </p>
                <p className='mb-2 mt-3 ml-4'>
                3.1.6. <u>Quadro C</u>: Nesse quadro, so apresentados dois tipos de grfico na mesma regio e uma tabela. Na regio do grfico, 
                h uma linha vertical que aponta o instante de tempo selecionado. O primeiro tipo  o grfico de linhas, sendo uma linha de 
                cor preta representante de todo o ensemble, ou seja, do conjunto inteiro, e as outras linhas (de outras cores) representantes 
                dos subconjuntos da amostra de parametrizaes. Por exemplo, se o processo fsico escolhido for Microfsica de Nuvens, 
                surgir uma linha preta e outras quatro linhas de cores distintas. A linha preta representa o volume mdio de chuva (eixo y) 
                simulado por todos os membros do ensemble na regio de interesse (que  a rea inteira ou a rea selecionada no Quadro B) ao 
                longo do tempo (eixo x). As outras quatro linhas do exemplo representam as mesmas variveis, mas consideram apenas os membros 
                pertencentes aos respectivos subconjuntos.
                </p>
                <p className='mb-2 ml-4'>
                O segundo tipo de grfico  o de rea. Por padro, esses grficos encontram-se invisveis de incio, mas o usurio poder 
                acionar os cones da tabela do lado direto do Quadro C, na coluna intitulada DP para visualiz-los. As cores so as mesmas 
                usadas no grfico de linhas, pois representam os mesmos subconjuntos e o mesmo ensemble. O limite superior de cada rea 
                representa os valores da mdia da chuva simulada ao longo do tempo somada a duas vezes o seu desvio-padro (M + 2DP), 
                enquanto o limite inferior representa os valores da mdia da chuva simulada ao longo do tempo subtrada de duas vezes o seu 
                desvio-padro (M - 2DP).
                </p>
                <p className='mb-2 ml-4'>
                A tabela (lado direito do Quadro C) apresenta (1) a legenda das cores, cujos cones tambm so botes para o usurio filtrar 
                as linhas e as reas visveis, (2) os valores explcitos do grfico no instante de tempo selecionado no Quadro A, (3) a 
                identificao do grupo que est representado no Quadro B.
                </p>
                <p className='mb-2 ml-4'>
                O usurio tem a opo de visualizar os valores dos eixos do grfico em escala local selecionando o boto Escala Local na parte superior esquerda do Quadro C.
                </p>

                <h5 className='mb-3 mt-3 ml-4'>3.2 Mdulo Probabilidade</h5>

                <p className='mb-2 ml-4'>
                'De modo anlogo ao mdulo Mdia, a Figura 3 ilustra um panorama da tela apresentada ao usurio quando o mdulo Probabilidade 
                 selecionado. Esse mdulo possui uma estrutura semelhante  do mdulo Mdia: h um boto na barra de navegao para seleo do 
                processo fsico que, por padro, tambm comea com Microfsica de Nuvens e apresenta trs quadros de anlise. O que muda nessa 
                interface so os dados apresentados, a estrutura do quadro C e um novo boto na barra de navegao. Com este boto, o usurio 
                seleciona limites inferiores de volumes de chuva para investigar, entre as opes: {'>='}5mm, {'>='}10mm, {'>='}20mm, {'>='}30mm. Por padro, 
                o sistema comea com o limite inferior {'>='} 5mm selecionado. Da mesma forma que em Mdia, o sistema selecionar a amostra de 
                parametrizaes relacionadas ao processo eleito, criar os subconjuntos de membros e montar a interface, mas, dessa vez, os dados 
                apresentados estaro de acordo com o limite inferior selecionado. O Quadro A apresenta matrizes de probabilidades no espao por membro, 
                o Quadro B expe mapas e o Quadro C, as Funes Massa de Probabilidade.
                </p>

                <p className='mb-2 ml-4' align='center'>
                    <img src={fig3} alt="some text" width='50%' height='50%'/>
                </p>
                <p className='mb-2 ml-4' align='center'>
                <i>Figura 3: Exemplo da tela do sistema criado para o mdulo Probabilidade na anlise do processo MIcrofsica de Nuvens. Quadro A (lado direito superior):Matrizes de Probabilidades no Espao por Membro; Quadro B (lado esquerdo): Mapas; Quadro C: Funes Massa de Probabilidade.</i>
                </p>
                <p className='mb-2 ml-4'>3.2.1. <u>Quadro A</u>: a estrutura e as interaes das matrizes com o Quadro B seguem as descries 
                realizadas para o Quadro A do mdulo Mdia, substituindo apenas o significado das suas cores. A cor de cada clula representa 
                o percentual do espao, ou seja, da rea de interesse em que aquele membro simulou volumes de chuva maiores ou iguais ao limite 
                inferior selecionado na barra de navegao naquele instante de tempo. Novamente, o usurio dever escolher um instante de tempo 
                e dois subconjuntos por vez para realizar investigaes na dimenso espacial. O instante de tempo neste mdulo tambm atualiza 
                o Quadro B e o Quadro C.
                </p>
                <p className='mb-2 ml-4'> 3.2.2. <u>Quadro B</u>: este quadro funciona da mesma maneira como a descrita no Quadro B do mdulo 
                Mdia, sendo as cores dos dois primeiros mapas referentes ao percentual de membros pertencentes aos subconjuntos eleitos no 
                Quadro A que geraram volumes maiores ou iguais ao limite inferior selecionado na barra de navegao no instante de tempo 
                escolhido. O Mapa 3 continua representando a diferena entre os valores do Mapa 1 e do Mapa 2, <b>nessa ordem</b>. 
                </p>
                <p className='mb-2 ml-4'> 3.2.3. <u>Quadro C</u>: Nesse quadro, so disponibilizados trs grupos de visualizao, sendo o 
                primeiro (da esquerda para a direita) referente a um instante de tempo anterior ao selecionado no Quadro A, o segundo ao 
                prprio instante de tempo selecionado e o terceiro ao instante seguinte. Em cada visualizao, h dois grficos de barras, 
                um com eixo x crescente para o lado direito e outro com o mesmo eixo crescente para o lado esquerdo. Esses grficos so 
                referentes aos subconjuntos selecionados no Quadro A e descrevem suas Funes Massa de Probabilidade. Por exemplo, se BMJ 
                foi selecionada no Mapa 1 e Grell-Freitas no Mapa 2 do Quadro A, a Funo Massa de Probabilidade de BMJ estar do lado 
                esquerdo e a de Grell-Freitas do lado direito. Cada barra tem sua base no eixo y e se encontra entre dois nmeros, que 
                indicam os limites inferior e superior de um intervalo de volume de chuva. Os valores do eixo x indicam o percentual de membros 
                do subconjunto que geraram valores pertencentes aos intervalos do eixo y para a rea de interesse (que pode ser atualizada no 
                Quadro B (quadro de mapas).
                </p>          
            </div>
        </Main>
            
    </React.Fragment>

    